.product {
  background: #FFF;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.product .product__description,
.product .product__action {
  -webkit-transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform, 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
}
.product:hover .product__description {
  -webkit-transform: translateY(-40px);
          transform: translateY(-40px);
}
.product:hover .product__action {
  -webkit-transform: none;
          transform: none;
}
.product-link {
  display: block;
  color: #3D3D3D;
}
.product-link:hover {
  color: #3D3D3D;
  text-decoration: none;
}
.product .product__image {
  width: 100%;
}
.product__image img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 225px;
}
.product__description {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 20px 20px 15px;
  background: #FFF;
}
.product__info {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
.product__description small {
  color: #808080;
}
.product__description h4 {
  margin: 3px 0 5px 0;
}
.product__price-cart {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: #51D2B7;
}
.product__price-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
.product__action {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.product__action button {
  border-radius: 0;
  width: 100%;
  display: block;
}

.admin-menu a {
  display: block;
  background: #EFDB06;
  border: 1px solid #F9E610;
  color: #3D3D3D;
  padding: 10px 15px;
  font-family: 'PT Sans', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 12px;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: -webkit-transform 200ms ease-in;
  transition: -webkit-transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}
.admin-menu a:hover {
  text-decoration: none;
  -webkit-transform: scaleX(1.02);
          transform: scaleX(1.02);
}
.admin-new, .admin-products {
  border-bottom: 2px solid #F5F5F5;
  padding: 30px 0;
}

.product-details {
  border-bottom: 2px solid #F5F5F5;
  padding: 30px 0;
}
.product-details__image {
}
.product-details__description {
  padding-top: 30px;
}
.product-details__description small {
  color: #808080;
}
.product-details__description h3 {
  margin: 5px 0 30px 0;
}
.product-details__price-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 30px;
}
.product-details__price-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
